<script>
import anime from "animejs";
import PhotoCollage from './PhotoCollage.vue';

export default {
  components: {
    PhotoCollage,
  },
  mounted() {
    window.addEventListener("scroll", this.profileImgScrollHandler);
    this.animate();
    this.animateSoftwareTitle();
    this.animateName();
  },
  methods: {
    hire() {
      const validationState = this.$refs.form.validate();
      if (validationState === true) {
        window.open('mailto:ghemid_m_dz@yahoo.fr?subject=Mission ( TJM : ' + this.tjm + ' )');
      }
    },
    profileImgScrollHandler() {
      if (
        document.body.scrollTop > 250 ||
        document.documentElement.scrollTop > 250
      ) {
        anime({
          targets: this.$refs.profileImg,
          translateY: -300,
          opacity: [1, 0],
          easing: "spring(1, 80, 10, 0)",
        });
      } else {
        anime({
          targets: this.$refs.profileImg,
          translateY: 0,
          opacity: 1,
          easing: "spring(1, 80, 10, 0)",
        });
      }
    },
    animateName() {
      let timeline = anime.timeline({
        easing: "linear",
        loop: true,
        duration: 3000,
      });

      timeline.add({
        targets: this.$refs.name,
        color: ["rgba(255, 255, 255, 100)", "rgba(0, 188, 212, 100)"],
      });
      timeline.add({
        targets: this.$refs.name,
        color: ["rgba(0, 188, 212, 100)", "rgba(255, 255, 255, 100)"],
      });
    },
    animate() {
      this.timelineAnimation = anime.timeline({
        easing: "easeInOutExpo",
      });
      this.timelineAnimation.add({
        targets: this.$refs.name,
        translateY: [-100, 0],
      });
      this.timelineAnimation.add(
        {
          targets: this.$refs.position,
          translateY: [1000, 0],
        },
        "-=500"
      );
      this.timelineAnimation.add(
        {
          targets: this.$refs.centerImg,
          opacity: [0, 1],
          duration: 2000,
        },
        "-=600"
      );

      this.timelineAnimation.add(
        {
          targets: this.$refs.fadeInAnimeRef,
          opacity: [0, 1],
          translateY: [1000, 0],
          duration: 2000,
        },
        "-=600"
      );
    },
    animateSoftwareTitle() {
      let timeline = anime.timeline({
        easing: "easeOutExpo",
        loop: true,
      });

      timeline.add({
        targets: this.$refs.craftsman,
        translateY: [-20, 0],
        opacity: [0, 1],
      });
      timeline.add({
        targets: this.$refs.craftsman,
        translateY: [0, 20],
        opacity: [1, 0],
      });

      timeline.add({
        targets: this.$refs.agile,
        translateY: [-20, 0],
        opacity: [0, 1],
      });
      timeline.add({
        targets: this.$refs.agile,
        translateY: [0, 20],
        opacity: [1, 0],
      });

      timeline.add({
        targets: this.$refs.fullstack,
        translateY: [-20, 0],
        opacity: [0, 1],
      });
      timeline.add({
        targets: this.$refs.fullstack,
        translateY: [0, 20],
        opacity: [1, 0],
      });

      timeline.add({
        targets: this.$refs.technicalAdvisor,
        translateY: [-20, 0],
        opacity: [0, 1],
      });
      timeline.add({
        targets: this.$refs.technicalAdvisor,
        translateY: [0, 20],
        opacity: [1, 0],
      });

      timeline.add({
        targets: this.$refs.leadDeveloper,
        translateY: [-20, 0],
        opacity: [0, 1],
      });
      timeline.add({
        targets: this.$refs.leadDeveloper,
        translateY: [0, 20],
        opacity: [1, 0],
      });
    },
  },
  data() {
    return {
      timelineAnimation: null,
      snackbar: true,
      tjm: null,
      minTjm: 599,
      nameRules: [
        v => (v && v > 599) || 'Minimum TJM is 600€',
      ],
    };
  },
  computed: {
    isMobile() {
      return this.$vuetify.breakpoint.xs;
    },
  },
};
</script>

